.main-section {
  background-color: white;
  width: 500px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding-bottom: 20px;
}

.clear-btn {
  color: red;
  font-weight: bold;
  border-style: none;
  margin: 20px 20px;
}

.grocery {
  width: 300px;
  height: 26px;
  border-radius: 0px;
  border-color: black;
}

.submit-btn {
  background-color: rgb(58, 174, 212);
  height: 25px;
  font-weight: bold;
  border-color: rgb(58, 174, 212);
}

.form-control {
  padding: 12px;
}
